// 该文件用于创建路由器
import VueRouter from 'vue-router'
// 引入组件
import About from '../components/About.vue'
import Home from '../components/Home.vue'
import News from '../page/News.vue'
import Message from '../page/Message.vue'
import Detail from '../page/Detail.vue'
//创建一个路由器
const router = new VueRouter({
  mode:'history',
routes:[
  {name:'about',
    path:'/about',
    component:About
  },
    {
      name:'home',
    path:'/home',
    component:Home,
    children:[{
      name:'news',
      path:'news',
      component:News,
      meta:{isAuth:true},
//       beforeEnter(to,from,next){
//       let arr = localStorage.getItem('school')
//   if(to.name ==='news' || to.name ==='message'){
//      if(arr === 'hafu' ){
//       console.log(to)
//       next()
//      }else{
//       alert('不对！')
//     }
//   }else{
//   next()
// }
//       }
    },{
      name:'message',
      path:'message',
      component:Message,
       meta:{isAuth:true},
      children:[{
        name:'detail',
        path:'detail',
        component:Detail,
        props($route){
          return {id:$route.query.id,title:$route.query.title}
        }
      }]
    }]
  },
]
})

