<template>
  <ul>
    <li :style="{opacity}">吓死个人！</li>
    <li>从没见过！</li>
    <li>自古以来绝无仅有！</li>
  </ul>
</template>

<script>
export default {
name:'News-new',
data(){
  return{
    opacity:1
  }
},
activated(){
this.timer = setInterval(()=>{
  this.opacity -=0.01
  if(this.opacity<=0)
  this.opacity = 1 
},20)
},
deactivated(){
clearInterval(this.timer)
},
// beforeRouteEnter(to,from,next){},
// beforeRouteLeave(to,from,next){}
}
</script>

<style scoped>
li{
  list-style: none;
}
</style>