<template>
  <div>
    <ul>
      <li v-for="value in messageList" :key="value.id">
        <!-- <router-link :to="`/home/message/detail?id=${value.id}&title=${value.title}`">{{value.title}}</router-link> -->
        <router-link :to="{
      name:'detail',
      query:{
        id:value.id,
        title:value.title
      }
        }">{{value.title}}</router-link>
        <button @click="pushshow(value)">push查看</button>
        <button @click="replaceshow(value)">replase查看</button>
      </li>
    </ul>
    <router-view></router-view>
  </div>
</template>

<script>
export default {
name:'Message-mes',
data(){
  return{
    messageList: [{
      id:'001',
      title:'震惊！！！'
      },{
      id:'002',
      title:'前所未有！！！'
      },{
      id:'003',
      title:'绝无仅有！！！'
      }]
  }
},
methods:{
  pushshow(value){
    this.$router.push({
      name: 'detail',
      query: {
        id: value.id,
        title: value.title
      }
    })
  },
  replaceshow(value){
    this.$router.replace({
          name: 'detail',
      query: {
      id: value.id,
        title: value.title
  }
    })
  }
}
}
</script>

<style scoped>
li{
  list-style: none;
}

</style>